.todo__header--btn {
  @apply w-8 h-8 rounded-full flex items-center justify-center transition ease-in-out duration-300 transform hover:rotate-45 focus:rotate-45;
}
.header__overlay {
  @apply bg-yellow-400 text-gray-700;
}

.todo__header--create .todo__header--btn {
  box-shadow: 0 0 0 3px rgba(17, 24, 39, 1);
}
.todo__header--create {
  @apply block my-2 transform;
}
.todo__header--create:nth-child(1) .todo__header--btn {
  @apply bg-yellow-400 text-gray-700;
}
.todo__header--create:nth-child(2) .todo__header--btn {
  @apply bg-red-400 text-gray-700;
}
.todo__header--create:nth-child(3) .todo__header--btn {
  @apply bg-green-400 text-gray-700;
}
.todo__header--create:nth-child(4) .todo__header--btn {
  @apply bg-purple-600 text-gray-300;
}

.todo__header--create:nth-child(2) {
  @apply -translate-y-10;
}
.todo__header--create:nth-child(3) {
  @apply -translate-y-20;
}
.todo__header--create:nth-child(4) {
  @apply -translate-y-28;
}

.todo__header--btns {
  @apply transition ease-in-out duration-300 opacity-0 pointer-events-none invisible;
}
.todo__header--visible {
  @apply transform translate-y-12 opacity-100 pointer-events-auto visible;
}
.todo__header--visible .todo__header--create {
  @apply -translate-y-0;
}
