.todo__fig {
  @apply w-full rounded-xl p-3;
}
.todo__fig--red {
  @apply bg-red-400;
}
.todo__fig--yellow {
  @apply bg-yellow-400;
}
.todo__fig--purple {
  @apply bg-purple-600;
}
.todo__fig--green {
  @apply bg-green-400;
}
.todo__fig--drop {
  @apply table p-3 bg-red-500 rounded-xl absolute top-8 right-0 w-32 invisible pointer-events-none opacity-0;
}
.todo__fig--yellow .todo__fig--drop {
  @apply bg-yellow-500;
}
.todo__fig--green .todo__fig--drop {
  @apply bg-green-500;
}
.todo__fig--red .todo__fig--drop {
  @apply bg-red-500;
}
.todo__fig--purple .todo__fig--drop {
  @apply bg-purple-300;
}

.todo__fig--active {
  @apply visible pointer-events-auto opacity-100;
}
.todo__fig--yellow .todo__fig--btn {
  @apply text-gray-600;
}
.todo__fig--green .todo__fig--btn {
  @apply text-green-700;
}
.todo__fig--red .todo__fig--btn {
  @apply text-red-800;
}
.todo__fig--purple .todo__fig--btn {
  @apply text-purple-300;
}

.todo__fig--purple .todo__fig--name,
.todo__fig--purple span {
  @apply text-gray-300;
}
.todo__fig .todo__fig--header {
  @apply flex items-center justify-between flex-nowrap;
}
.todo__fig .todo__fig--name {
  @apply pr-2 truncate;
}
.todo__fig p {
  @apply text-gray-800 text-sm;
}

.todo__fig--purple p {
  @apply text-gray-300;
}

.todo__fig--yellow figcaption {
  @apply border-yellow-300;
}
.todo__fig--yellow figcaption h5 {
  @apply text-gray-600;
}

.todo__fig--red figcaption {
  @apply border-red-300;
}
.todo__fig--red figcaption h5 {
  @apply text-gray-600;
}

.todo__fig--green figcaption {
  @apply border-green-300;
}
.todo__fig--green figcaption h5 {
  @apply text-gray-600;
}

.todo__fig--purple figcaption {
  @apply border-purple-300;
}
.todo__fig--purple figcaption h5 {
  @apply text-gray-300;
}

.todo__fig--container {
  @apply container mx-auto p-4 grid xl:grid-cols-5 lg:grid-cols-4 md:grid-cols-3 sm:grid-cols-2 grid-cols-1 gap-5 font-medium;
}
